import React from "react";
import { useUniversalRouter } from "./routerViteGatsbyUniversal";

type UniversalLinkProps = React.AnchorHTMLAttributes<HTMLAnchorElement> & {
  to: string;
};

export const UniversalLink: React.FC<UniversalLinkProps> = ({
  to,
  onClick,
  children,
  ...rest
}) => {
  const { go } = useUniversalRouter();

  const handleClick = (e: React.MouseEvent<HTMLAnchorElement>) => {
    onClick?.(e);
    if (e.defaultPrevented) return;

    e.preventDefault();
    go(to);
  };

  return (
    <a href={to} onClick={handleClick} {...rest}>
      {children}
    </a>
  );
};
