import React, { useState } from "react";
import { CheckCircle, Mail, Loader, AlertCircle } from "lucide-react";
import "../../styles/auth.css";
import { useLanguage } from "../../i18n/LanguageContext";
import { UniversalLink as Link } from "../../database/UniversalLink";
import { useUniversalRouter } from "../../database/routerViteGatsbyUniversal";

export function EmailConfirmation() {
  const { go } = useUniversalRouter();
  const { t } = useLanguage();

  const [status, setStatus] = useState<"idle" | "loading" | "success" | "error">("idle");
  const [resendEmail, setResendEmail] = useState("");
  const [resendLoading, setResendLoading] = useState(false);
  const [resendSuccess, setResendSuccess] = useState(false);

  const handleVerifyClick = () => {
    setStatus("loading");
    
    // Show loading for 2 seconds
    setTimeout(() => {
      setStatus("success");
      // Redirect to app after showing success
      setTimeout(() => {
        go("/app");
      }, 1500);
    }, 2000);
  };

  const handleResendEmail = (e: React.FormEvent) => {
    e.preventDefault();
    setResendLoading(true);
    setResendSuccess(false);

    // Simulate resend
    setTimeout(() => {
      if (resendEmail) {
        setResendSuccess(true);
        setResendEmail("");
        setResendLoading(false);
        // Hide success message after 4 seconds
        setTimeout(() => {
          setResendSuccess(false);
        }, 4000);
      }
    }, 1000);
  };
  return (
    <div className="auth-container">
      <div className="auth-bg">
        <div className="auth-gradient-orb orb-1"></div>
        <div className="auth-gradient-orb orb-2"></div>
        <div className="auth-gradient-orb orb-3"></div>
      </div>

      <Link to="/" className="auth-back-link">
        <svg width="20" height="20" viewBox="0 0 20 20" fill="none">
          <path
            d="M15 10H5M5 10L10 15M5 10L10 5"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
          />
        </svg>
        Back to Home
      </Link>

      <div className="auth-card">
        {status === "idle" && (
          <div className="auth-header">
            <div className="auth-icon-badge">
              <Mail className="auth-mail-icon" size={32} />
            </div>
            <h1 className="auth-title">Confirm Your Email</h1>
            <p className="auth-subtitle">
              Click the button below to verify your email address and complete your registration.
            </p>
          </div>
        )}

        {status === "loading" && (
          <div className="auth-header">
            <div className="auth-icon-badge auth-loading-badge">
              <Loader className="auth-spinner-icon" size={32} />
            </div>
            <h1 className="auth-title">Verifying…</h1>
            <p className="auth-subtitle">
              Please wait while we confirm your email address.
            </p>
          </div>
        )}

        {status === "success" && (
          <div className="auth-header auth-success-state">
            <div className="auth-icon-badge auth-success-badge">
              <CheckCircle size={32} />
            </div>
            <h1 className="auth-title">Email Verified!</h1>
            <p className="auth-subtitle">
              Your email has been confirmed. Redirecting to your dashboard…
            </p>
          </div>
        )}

        {status === "error" && (
          <div className="auth-header auth-error-state">
            <div className="auth-icon-badge auth-error-badge">
              <AlertCircle size={32} />
            </div>
            <h1 className="auth-title">Link Expired</h1>
            <p className="auth-error-message">
              Your confirmation link has expired. Please enter your email below to receive a new one.
            </p>

            <form onSubmit={handleResendEmail} className="auth-form auth-resend-form">
              <div className="auth-field">
                <label htmlFor="resend-email" className="auth-label">
                  <Mail size={14} />
                  Email Address
                </label>
                <div className="auth-input-wrapper">
                  <input
                    id="resend-email"
                    type="email"
                    className="auth-input"
                    placeholder="you@example.com"
                    value={resendEmail}
                    onChange={(e) => setResendEmail(e.target.value)}
                    required
                    disabled={resendLoading}
                  />
                </div>
              </div>

              {resendSuccess && (
                <div className="auth-success-message">
                  ✓ Confirmation email sent! Check your inbox.
                </div>
              )}

              <button type="submit" className="auth-submit-btn" disabled={resendLoading}>
                {resendLoading ? (
                  <>
                    <span className="auth-spinner"></span>
                    Sending…
                  </>
                ) : (
                  <>
                    <Mail size={18} />
                    Resend Confirmation Email
                  </>
                )}
              </button>
            </form>

            <div className="auth-footer">
              <p>
                Back to{" "}
                <Link to="/login" className="auth-link">
                  Login
                </Link>
              </p>
            </div>
          </div>
        )}

        {status === "idle" && (
          <button onClick={handleVerifyClick} className="auth-submit-btn">
            <Mail size={18} />
            Verify Email
          </button>
        )}

        {status === "idle" && (
          <div style={{ marginTop: "20px", textAlign: "center" }}>
            <button 
              onClick={() => setStatus("error")} 
              style={{
                background: "none",
                border: "none",
                color: "#60a5fa",
                fontSize: "0.9rem",
                cursor: "pointer",
                textDecoration: "underline",
                fontWeight: "500"
              }}
            >
              Test Error State →
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
